import Handwriting from "../../static/js/signature.js"
export default {
	data(){
		return{
			lineColor:'black',
			slideValue:50,
			handwriting:'',
			selectColor:'black',
			color:'',
			showimg:'',
			share_popup:false,
		}
	},
	onLoad() {
		this.handwriting = new Handwriting({
		  lineColor: this.lineColor,
		  slideValue: this.slideValue, // 0, 25, 50, 75, 100
		  canvasName: 'handWriting',
		})
	},
	methods:{
	 // 选择画笔颜色
	  selectColorEvent(event) {
		  this.selectColor = event;
		if(event=='black'){
			this.color= '#1A1A1A'
		}else if(event=='red'){
			this.color= '#ca262a'
		} 
		this.handwriting.selectColorEvent(this.color)
	  },
	  retDraw() {
		this.handwriting.retDraw()
	  },
	  // 笔迹粗细滑块
	  updateValue(e) {
		  console.log(e.detail);
		  this.slideValue = e.detail.value;
		this.handwriting.selectSlideValue(this.slideValue);
	  },
	  uploadScaleStart(event){
		  this.handwriting.uploadScaleStart(event)
	  },
	  uploadScaleMove(event){
		  this.handwriting.uploadScaleMove(event)
	  },
	  uploadScaleEnd(event){
		  this.handwriting.uploadScaleEnd(event)
	  },
	  subCanvas(){
		  this.handwriting.saveCanvas().then(res=>{
			  this.showimg = res;
			  console.log(res);
		  }).catch(err=>{
			  console.log(err);
		  });		  
		  
	  },
	}
}
